<template>
  <stroke-page
    v-if="!isLoading"
    :leaderboard="getStrokeLeaderboard"
    :money="objectPurse"
    :link="'StrokeOverallPlayer'"
    :title="'Stroke'"
  />
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import StrokePage from './Page'

export default {
	name: 'StrokeOverallLeaderboard',
  components: {
   StrokePage
  },
  data () {
    return {
      objectPurse: 0,
      isLoading: true,
      year: "Overall"
    }
  },

  computed: {
     ...mapGetters({
      getStrokeLeaderboard: 'leaderboards/overall/getStrokeLeaderboard',
      strokePurse: 'leaderboards/overall/getStrokePurse'
     })
  },
  methods: {
  },

  mounted: function () {
    this.$store.dispatch(
      'leaderboards/overall/LOAD_STROKE_LEADERBOARD'
    ).then(response => {
      this.objectPurse = this.strokePurse
      this.isLoading = false
    })
  }
}
</script>
<style scoped>
.lb-card {
  border-radius: 0;
  top: 0;
  left: 0;
  height: 100vh;
  overflow: scroll;
  width: 100vw;
  background-color: #9FB8CE;
  color: #f1f1f1;
}
</style>



